export function openReservationsByParam() {
  var reservationsQueryString = window.location.search;

  if (typeof URLSearchParams !== "undefined") {
    var reservationsUrlParams = new URLSearchParams(reservationsQueryString);
    var reservationsParam = reservationsUrlParams.get("reservations");

    if (reservationsParam) {
      document.getElementById("reservations-button").click();
    }
  }
}
