import ScrollMagic from "scrollmagic";
import "scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap";
import TimelineMax from "gsap/TimelineMax";

export function featureBoxParallax() {
  if ($(".front__feature").length) {
    const controller = new ScrollMagic.Controller();
    const $block = $(".wrap-feature");
    var clientWidth = window.innerWidth;

    if (clientWidth > 1024) {
      new ScrollMagic.Scene({
        triggerElement: ".front__feature",
        duration: "160%",
        triggerHook: 1,
      })
        .setTween($block, { top: "-100%" })
        .addTo(controller);
    } else {
      new ScrollMagic.Scene({
        triggerElement: ".front__feature",
        duration: "300%",
        triggerHook: 1,
      })
        .setTween($block, { top: "-80%" })
        .addTo(controller);
    }
  }
}
