import $ from "jquery";
import { slick } from "slick-carousel/slick/slick";

export function sliderInit() {
  let slider = $(".front__slider");

  if (!slider.length) {
    slider = $(".front-2__slider");
  }

  if (!slider.length) {
    return false;
  }

  slider.slick({
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: false,
    //   autoplay: true,
    autoplaySpeed: 6000,
    pauseOnHover: false,
    prevArrow: `<button type="button" class="slick-prev">
            <svg width="15px" height="27px" viewBox="0 0 15 27" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <g id="Welcome" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                    <g id="ENTREPRENEURS" transform="translate(-671.000000, -1021.000000)" stroke="#FFFFFF">
                        <g id="Group-2" transform="translate(671.000000, 1022.000000)">
                            <polyline id="Path-2-Copy" transform="translate(7.500000, 12.500000) scale(-1, 1) translate(-7.500000, -12.500000) " points="0 0 15 12.5583143 0 25"></polyline>
                        </g>
                    </g>
                </g>
            </svg>
        </button>`,
    nextArrow: `<button type="button" class="slick-next">
            <svg width="15px" height="27px" viewBox="0 0 15 27" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                <g id="Welcome" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                    <g id="ENTREPRENEURS" transform="translate(-922.000000, -1021.000000)" stroke="#FFFFFF">
                        <g id="Group-2" transform="translate(671.000000, 1022.000000)">
                            <polyline id="Path-2" points="251 0 266 12.5583143 251 25"></polyline>
                        </g>
                    </g>
                </g>
            </svg>
      	</button>`,
  });

  slider.on("afterChange", function () {
    //change color here
    const activeSlide = $(".slick-active .front__main-area-desktop");

    if (
      !activeSlide.hasClass("lazyload") &&
      !activeSlide.hasClass("lazyloaded") &&
      !activeSlide.hasClass("lazyloading")
    ) {
      activeSlide.addClass("lazyload");
    }

    const activeSlideMobile = $(".slick-active .front__main-area-mobile");
    if (
      !activeSlideMobile.hasClass("lazyload") &&
      !activeSlideMobile.hasClass("lazyloaded") &&
      !activeSlideMobile.hasClass("lazyloading")
    ) {
      activeSlideMobile.addClass("lazyload");
    }
  });
}
