export function jobFormFix() {
  // Check for Job form
  if ($("#form_5ae8cf4c75bad").length > 0) {
    const hidden_form_field = $("#acf-field_5bf5b0dab9236");
    if (hidden_form_field.length > 0) {
      // added via advanced forms filter
      const hidden_job_title = $(
        ".acf-hidden input[name='job_title_hidden']"
      ).val();
      hidden_form_field.val(hidden_job_title);
    }
  }
}
