import List from "list.js"

export function restaurantSearch() {
  // http://listjs.com/
  const options = {
    valueNames: ["place", "province", "address"],
  }

  $(".utility-menu__item--reservationButton, .list-filter-button").on(
    "click",
    function () {
      // Featherlight copies the hidden module into the lightbox so we need to create a unique id
      $(".featherlight-content .listFilter").attr("id", "listFilter")
      new List("listFilter", options)
    }
  )
}
