export function accordion() {
  $(".accordion").on("click", function () {
    $(this).toggleClass("active");

    var panel = $(this).next();

    if (panel.css("max-height") !== "0px") {
      panel.css("max-height", 0);
    } else {
      // const newValue = panel.prop("scrollHeight") + "px";
      panel.css("max-height", "none");
    }
  });
}
