export function mailchimpSignup() {
  $ = jQuery;

  // Hide all responses
  $(".newsletterButton .button").on("click", function () {
    $(".newsletter .response").css("display", "none");
    $(".newsletter input").parent().removeClass("required");
  });

  $("#newsletter-form").on("submit", function (e) {
    e.preventDefault();

    // Hide all responses
    $(".newsletter .response").css("display", "none");

    var values = {};
    $.each($(this).serializeArray(), function (i, field) {
      values[field.name] = field.value;
    });

    // Validate form fields
    var error = false;
    var email = values["email"];
    var first_name = values["FirstName"];
    var last_name = values["LastName"];
    var location = values["Location"];

    if (email == "") {
      error = true;
      $(".newsletter-email").parent().addClass("required");
    } else {
      $(".newsletter-email").parent().removeClass("required");
    }

    if (first_name == "") {
      error = true;
      $(".newsletter-first-name").parent().addClass("required");
    } else {
      $(".newsletter-first-name").parent().removeClass("required");
    }

    if (last_name == "") {
      error = true;
      $(".newsletter-last-name").parent().addClass("required");
    } else {
      $(".newsletter-last-name").parent().removeClass("required");
    }

    if (location == "") {
      error = true;
      $(".newsletter-location").parent().addClass("required");
    } else {
      $(".newsletter-location").parent().removeClass("required");
    }

    if (error == true) {
      $(".newsletter .response--error").html(
        "Please fill out all required fields."
      );
      $(".newsletter .response--error").css("display", "block");
    } else if (isValidEmailAddress(email)) {
      const url =
        $(`#newsletter-form`).attr("action") +
        `?email=${email}&FirstName=${first_name}&LastName=${last_name}&Location=${location}&goto=&iehack=&#9760;`;

      $(".newsletter-fields").hide();

      $(".newsletter .response--success").html(
        "You have subscribed successfully."
      );
      $(".newsletter .response--success").fadeIn();

      window.open(url, "_blank");
    } else {
      $(".newsletter .response--error").html("Oops...Your email is not valid.");
      $(".newsletter .response--error").css("display", "block");
    }
  });
}

function isValidEmailAddress(emailAddress) {
  var pattern = new RegExp(
    /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
  );
  return pattern.test(emailAddress);
}
