export function createReservationsIframe() {
  // creates the open table iframes for locations page
  $(".storelocator__store-opentable").on("click", function() {
    var storeOpenTableId = $(this).data("opentableid");
    var storeOpenTableLink =
      "https://www.opentable.ca/widget/reservation/canvas?rid=" +
      storeOpenTableId +
      "&type=standard&theme=tall&overlay=false&domain=ca&lang=en-CA&r3abvariant=false&r3uid=Xk-13oE9Uk&newtab=false&disablega=false";

    var reservationsIframe =
      '<iframe src="' +
      storeOpenTableLink +
      '" id="storelocator__opentable__iframe" width="288" height="490" frameborder="0" scrolling="no" tabindex = "-1" ></iframe >';

    $(".storelocator__opentable").html(reservationsIframe);
  });
}
