export function menuFilter() {
  $ = jQuery;

  hideSelectCTA();

  /**************************
   *  Province Select
   **************************/

  $(".menu-province").on("change", function() {
    // Remove placeholder "Select a province" if selected for the first time
    $(".menu-province .placeholder").remove();

    // Get selected province
    var province = $(this)
      .find(":selected")
      .attr("data-value");

    //Change restaurant in second select box
    $(".menu-restaurant option").addClass("is-hidden");
    $(".menu-restaurant option").attr("disabled", true);

    $(".menu-restaurant option[data-province=" + province + "]").removeClass(
      "is-hidden"
    );
    $(".menu-restaurant option[data-province=" + province + "]").attr(
      "disabled",
      false
    );

    //Select first element
    $(".menu-restaurant option[data-province=" + province + "]")
      .first()
      .prop("selected", true);

    changePrice();

    hideSelectCTA();

    $(".menu-restaurant").trigger("change");
  });

  /**************************
   *  Restaurant Select
   **************************/
  let hiddenOptions = [];
  $(".menu-restaurant").on("change", function() {
    hideSelectCTA();

    // Hide all menus
    $(".menu-type option").addClass("is-hidden");
    $(".menu-type option").attr("disabled", true);

    const foodMenuSelect = $(".menu-type > [label=Food]").parent("select");

    // restore hidden select options
    if (hiddenOptions.length > 0) {
      for (let option of hiddenOptions) {
        $(foodMenuSelect).toggleOption(option);
      }
    }
    hiddenOptions = [];

    // Get food menu categories
    var food_menus = $(this)
      .find(":selected")
      .attr("data-menu-food");
    var food_menus_array = food_menus.split(" ");

    $.each(food_menus_array, function(index, value) {
      $(".menu-type option[data-value='" + value + "']").removeClass(
        "is-hidden"
      );
      $(".menu-type option[data-value='" + value + "']").attr(
        "disabled",
        false
      );
    });

    // Get drink menu categories
    var drink_menus = $(this)
      .find(":selected")
      .attr("data-menu-drinks");
    var drink_menus_array = drink_menus.split(" ");

    $.each(drink_menus_array, function(index, value) {
      $(".menu-type option[data-value='" + value + "']").removeClass(
        "is-hidden"
      );
      $(".menu-type option[data-value='" + value + "']").attr(
        "disabled",
        false
      );
    });

    const hiddenMenus = foodMenuSelect.find(".is-hidden");
    // remove hidden select options from select until they're needed again.
    // https://github.com/glittle/sunwapta.jquery.toggleOption
    hiddenMenus.each(function(index, optionEl) {
      hiddenOptions.push($(this).val());
      $(foodMenuSelect).toggleOption($(this).val());
    });

    // Check if selected menu is hidden
    var selected = $(".menu-type").find(":selected");
    if (selected.hasClass("is-hidden")) {
      // if true select the first menu without class is-hidden
      $(".menu-type option:not(.is-hidden):first").prop("selected", true);
      showSelectedDishes();
    }

    changePrice();
    $(".menu-type").trigger("change");
  });

  /**************************
   *  Menu Select
   **************************/
  $(".menu-type").on("change", function() {
    hideSelectCTA();
    showSelectedDishes();
    changeBannerImage();

    // Get new menu type
    var description = $(".menu-type")
      .find(":selected")
      .attr("data-description");

    $(".menu__description").html(description);
  });

  /**************************
   *  OnLoad if restaurant is selected
   **************************/
  if (typeof defaultRestaurant !== "undefined" && defaultRestaurant > "") {
    showSelectedDishes();
    changePrice();
    $(".menu-restaurant").trigger("change");
  }

  /**************************
   *  Functions
   **************************/

  // function restoreHiddenMenuItems() {
  //   const foodMenuSelect = $(".menu-type > [label=Food]").parent("select");

  //   // restore hidden select options
  //   if (hiddenMenuItems.length > 0) {
  //     for (let option of hiddenMenuItems) {
  //       $(foodMenuSelect).toggleOption(option);
  //     }
  //   }
  //   hiddenMenuItems = [];
  // }

  // function removeHiddenMenuItems() {
  //   const foodMenuSelect = $(".menu-type > [label=Food]").parent("select");

  //   const hiddenMenus = foodMenuSelect.find(".is-hidden");
  //   // remove hidden select options from select until they're needed again.
  //   // https://github.com/glittle/sunwapta.jquery.toggleOption
  //   hiddenMenus.each(function(index, optionEl) {
  //     hiddenMenuItems.push($(this).val());
  //     $(foodMenuSelect).toggleOption($(this).val());
  //   });
  // }

  function changeBannerImage() {
    var styles = $(".menu-type")
      .find(":selected")
      .attr("data-style");

    var bgset = $(".menu-type")
      .find(":selected")
      .attr("data-bgset");

    if (bgset !== $(".banner").attr("data-bgset")) {
      $(".banner").attr("style", styles);
      $(".banner").attr("data-bgset", bgset);
      $(".banner").addClass("lazyload");
    }
  }

  function hideSelectCTA() {
    var selected = $(".menu-restaurant").find(":selected");
    if (selected.hasClass("placeholder")) {
      $(".menu__select-cta").removeClass("is-hidden");
    } else {
      $(".menu__select-cta").addClass("is-hidden");
    }
  }

  function showSelectedDishes() {
    // Get new menu type
    var selected = $(".menu-type")
      .find(":selected")
      .attr("data-value");

    $(".menu__menu-container")
      .not(".nutritional-info__edges")
      .addClass("is-hidden");
    $(".menu__menu-container[data-value='" + selected + "']").removeClass(
      "is-hidden"
    );
  }

  function changePrice() {
    $(".dish__price-subitem").addClass("is-hidden");

    // Get menu type and add suffix if drink
    var selected = $(".menu-type").find(":selected");
    if (selected.hasClass("drink")) {
      var suffix = "-drinks";
    } else {
      var suffix = "";
    }

    var region = $(".menu-restaurant")
      .find(":selected")
      .attr("data-region" + suffix);

    $.each($(".dish"), function() {
      var new_price = $(this).attr("data-" + region);

      if (typeof new_price === "undefined") {
        new_price = "";
        $(this).addClass("is-hidden");
      } else {
        $(this).removeClass("is-hidden");

        $(this)
          .find(".dish__price")
          .html(new_price);
      }
    });

    $(".dish__price-subitem[data-region='" + region + "']").removeClass(
      "is-hidden"
    );
  }
}
